import React, { Component } from 'react';
import {HeaderWrap,NavWrap} from './styled'
class Header extends Component {
    render() {
        return (
            <HeaderWrap>
                <NavWrap>
                    <div className="image">
                        <img src="https://img.alicdn.com/tfs/TB1Zv8_lxSYBuNjSspjXXX73VXa-390-63.png" alt=""></img>
                    </div>
                    <i className="border"></i>
                    <i className="website">社招官网</i>
                    <ul>
                        <li className="change">首&nbsp;&nbsp;页</li>
                        <li>社会招聘</li>
                        <li>校园招聘</li>
                        <li>了解阿里</li>
                        <li>个人中心</li>
                    </ul>
                    <div className="login">
                        <i>欢迎来到阿里巴巴集团招聘!</i>
                        <a href="www.baidu.com">登录</a>
                        <a href="www.baidu.com">注册</a>
                    </div>
                </NavWrap>
            </HeaderWrap>
        )
    }
}

export default Header;