import styled from 'styled-components'

export const HeaderWrap = styled.div`
    height:52px;
    background:#2C2F33;
    /* display:flex; */
    
`
export const NavWrap = styled.div`
    height:52px;
    width:1000px;
    display:flex;
    
    margin: 0 auto;
    .image{
        width:123.8px;
        height:52px;
        img{
        width:123.8px;
        height:20px;
        margin-top:15px;
        }
    }
    .border{
        height:20px;
        width:3px;
        font-size:20px;
        background:#fff;
        margin:0 10px;
        opacity:0.8;
        margin-top:15px;
    }
    .website{
        color:white;
        font-size:14px;
        line-height:52px;
        opacity:0.8;
    }
    ul{
        display:flex;
        .change{
            color:#f37327;
        }
        li{
            font-family:PingFangSC-Regular;
            width:86px;
            height:39px;
            color:white;
            font-size:14px;
            line-height:52px;
            opacity:0.8;
            text-align:center;
            margin:0 0 0 4px;
        }
        li:hover{
            color:#f37327;
        }
    }
    .login{
        
        height:52px;
        color:white;
        line-height:52px;
        opacity:0.8;
        font-size:12px;
        i{
            margin-left:70px;
            margin-right:10px;
        }
        a{
            color:white;
            opacity:0.8;
        }
    }
`