import styled from 'styled-components'

export const AdvertiseWrap = styled.div`
    height:364px;
    background:#fff;
    display:flex;
    justify-content:center;
    align-items:center;
`
export const NewWrap = styled.div`
    height:300px;
    width:1180px;
    display:flex;
`
export const NewContentWrap = styled.div`
    box-shadow: 0 0 15px 0 rgba(31,56,88,0.08);
    width:850px;
    height:300px;
    .title{
        width:850px;
        height:50px;
        font-size:14px;
        padding:0 14px;
        line-height:50px;
        .more{
            float:right;
        }
    }
    ul{
        padding-left:10px;
        li{
            display:flex;
            width:850px;
            height:50px;
            flex:1;
            a{
                width:400px;
                height:50px;
                font-size:14px;
                margin-right:90px;
                line-height:50px;
            }
            .location{
                width:202px;
                height:50px;
                margin:0 70px 0 0;
                color:rgba(0,0,0,0.6);
                line-height:50px;
            }
            .time{
                width:70px;
                height:50px;
                text-align:right;
                color:rgba(0,0,0,0.6);
                line-height:50px;
            }
        }
    }
`
export const ImageWrap = styled.div`
    width:310px;
    height:300px;
    margin-left:20px;
    .cloud{
        box-shadow: 0 0 15px 0 rgba(31,56,88,0.08);
        width:310px;
        height:140px;
        display:flex;
        justify-content:center;
        align-items:center;
        margin: 0 0 20px;
        background:#fff;
        img{
            width:179px;
            height:68px;
        }
    }
    .intelligence{
        box-shadow: 0 0 15px 0 rgba(31,56,88,0.08);
        width:310px;
        height:140px;
        display:flex;
        justify-content:center;
        align-items:center;
        background:#fff;
        img{
            width:260px;
            height:36.83px;
        }
    }
    

`