import React, { Component } from 'react';
import {SearchWrap,MidBoxWrap} from './styled'
class Search extends Component {
    render() {
        return (
            <SearchWrap>
                <img src="https://img.alicdn.com/tfs/TB14TEEm7CWBuNjy0FaXXXUlXXa-1440-478.png" alt=""/>
                <MidBoxWrap>
                    <div>
                        <p>If not now,when?</p>
                        <p>If not me,who?</p>
                        <p className="chinese">此时此刻,非我莫属!</p>
                        <div className="search">
                            <input type="text" placeholder="请输入你的关键词"/>
                            <button>搜索</button>
                        </div>
                        <div className="hot_text">
                            <span>热门搜索:</span>
                            <ul>
                                <li>JAVA</li>
                                <li>IOS</li>
                                <li>数据</li>
                                <li>安全</li>
                                <li>搜索</li>
                                <li>算法</li>
                                <li>运营</li>
                                <li>视觉</li>
                                <li>交互</li>
                                <li>前端</li>
                            </ul>
                        </div>
                    </div>
                </MidBoxWrap>
            </SearchWrap>

        )
    }
}

export default Search;