import styled from 'styled-components'

export const SearchWrap = styled.div`
    display:flex;
    width:100%;
    position:relative;
    img{
        width:100%;
        background:rgba(31,56,88,0.4)
    }
`
export const MidBoxWrap = styled.div`
    width:490px;
    height:478px;
    position:absolute;
    z-index:1;
    left:50%;
    top:50%;
    display:flex;
    margin-left:-245px;
    margin-top:-145px;
    p{
        line-height:56px;
        font-size:52px;
        color:#fff;
        font-family:'Rufina-Bold';
    }
    .chinese{
        font-size:34px;
    }
    .search{
        display:flex;
        width:490px;
        height:60px;
        background:rgba(0,0,0,0.1);
        
        align-items:center;
        input{
            margin-left:10px;
            width:368px;
            height:40px;
            border-radius:3px;
            padding:10px 0 6px;
            outline:none;
            text-indent:10px;
            border:none;
        }
        button{
            width:92px;
            height:40px;
            color:#fff;
            background:#f37327;
            font-size:14px;
            border-radius:3px;
            margin-left:10px;
            border:none;
            
        }
    }
    .hot_text{
        width:490px;
        height:38px;
        display:flex;
        color:#eee;
        margin-top:10px;
        font-size:12px;
        display:flex;
        justify-content:center;
        span{
            margin-right: 10px;
        }
        ul{
            display:flex;
            
            li{
                padding-right:10px;
                font-size:12px;
                
            }
            li:hover{
                color:#F37327;
            }
        }
    }
`